h1, h2 {
	text-align: center;
}

table.responsiveTable {
  border: 1px solid #1C6EA4;
  background-color: #EEEEEE;
  width: 100%;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  border-collapse: collapse;
  font-family: Helvetica
}
table.responsiveTable td, table.responsiveTable th {
  border: 1px solid #AAAAAA;
  padding: 3px 2px;
}
table.responsiveTable tbody td {
  font-size: 13px;
}
table.responsiveTable tr:nth-child(even) {
  background: #D0E4F5;
}
table.responsiveTable thead {
  background: #1C6EA4;
  background: -moz-linear-gradient(top, #5592bb 0%, #327cad 66%, #1C6EA4 100%);
  background: -webkit-linear-gradient(top, #5592bb 0%, #327cad 66%, #1C6EA4 100%);
  background: linear-gradient(to bottom, #5592bb 0%, #327cad 66%, #1C6EA4 100%);
  border-bottom: 2px solid #444444;
}
table.responsiveTable thead th {
  font-size: 15px;
  font-weight: bold;
  text-align: center;
  color: #FFFFFF;
  border-left: 2px solid #D0E4F5;
}
table.responsiveTable thead th:first-child {
  border-left: none;
}

table.responsiveTable tfoot {
  font-size: 14px;
  font-weight: bold;
  color: #FFFFFF;
  background: #D0E4F5;
  background: -moz-linear-gradient(top, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
  background: -webkit-linear-gradient(top, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
  background: linear-gradient(to bottom, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
  border-top: 2px solid #444444;
}
table.responsiveTable tfoot td {
  font-size: 14px;
}
table.responsiveTable tfoot .links {
  text-align: right;
}
table.responsiveTable tfoot .links a{
  display: inline-block;
  background: #1C6EA4;
  color: #FFFFFF;
  padding: 2px 8px;
  border-radius: 5px;
}